/* ==========================================================================
   Base
   ========================================================================== */

* {
  border: 1px solid var(--colors-error-background);
  border: 1px solid var(--colors-light);
}

html {
  font-family: "Segoe UI", Segoe, Tahoma, sans-serif;
  font-size: 1rem;
  line-height: 1.44;
}

body {
  margin: 0;
  /**
   * Root em units
   * 
   * A root unit (rem) unit is relative to the root font size,
   * wheras an em unit is relative to it's parent.
   * 
   * Ex: root font size = 16px
   *     1.75rem        = 28px 
   */
  font-size: 1.75rem;
}
/* ==========================================================================
   Layout
   ========================================================================== */

.flexbox,
.flexbox-item {
  display: -webkit-flex; /* Chrome 21+ */
  display: flex; /* Opera 12.1, Firefox 22+ */

  /* Align and distribute flex items on the main axis (horizontally) */
  -webkit-justify-content: space-between;
  justify-content: space-between;

  /* Align and distribute flex items on the cross axis (vertically) */
  -webkit-align-items: center;
  align-items: center;
}

.flexbox-item {
  /**
   * Viewport Units
   * 
   * A viewport unit (vh/vw) is relative to the viewport size
   * 1 viewport unit = 1% of the viewport 
   */
  height: 80vh;
}

.header,
.footer {
  height: 10vh;
  color: black;
  background-color: var(--colors-dark-light);
  /* background-color: #666; */
  -webkit-transition: font-size 0.5s linear;
  transition: font-size 0.5s linear;
}

.nav,
.aside {
  /* prevent shrinking and growing */
  -webkit-flex: none;
  flex: none;
  width: 180px;
}

.nav {
  justify-content: space-around;
  /* background-color: var(--colors-focused); */
  background-color: var(--colors-secondaryBackground);
}

.nav.flexbox-item {
  flex-direction: column;
}

.col {
  /* flex-direction: column; */
}

.aside {
  /* background-color: #ff6633; */
  background-color: var(--colors-secondaryBackground);
}

.section {
  /* allow shrinking and growing */
  -webkit-flex: auto;
  flex: auto;
  width: auto;
  /* background-color: #d6d6d6; */
  background-color: var(--colors-secondaryBackground);
  /* background-color: var(--colors-warning-background); */
}

/* Apply the following styles up to a viewport width of 560px */
@media (max-width: 35em) {
  .flexbox {
    /* place all boxes in a single column, and reverse their order */
    -webkit-flex-direction: column;
    flex-direction: column;
  }

  .flexbox-item {
    width: 100vw;
    height: 26.6666667vh;
  }

  .header,
  .footer {
    font-size: 1.25rem;
  }

  /* reorder the items for a good mobile UX */
  .nav {
    -webkit-order: 3;
    order: 3;
  }

  .section {
    -webkit-order: 1;
    order: 1;
  }

  .aside {
    -webkit-order: 2;
    order: 2;
  }
}
/* ==========================================================================
   Theme
   ========================================================================== */

button {
  border: 2px solid darkgreen;
  width: 10vw;
  height: 5rem;
  /* border-radius: 10px; */
}

HTML CSS Result * {
  box-sizing: border-box;
}

/* basic stylings ------------------------------------------ */
body {
  background: url(https://scotch.io/wp-content/uploads/2014/07/61.jpg);
}
.container {
  font-family: "Roboto";
  width: 90vw;
  margin: 30px auto 0;
  display: block;
  background: #fff;
  /* padding: 10px 50px 50px; */
}
h2 {
  text-align: center;
  margin-bottom: 50px;
}
h2 small {
  font-weight: normal;
  color: #888;
  display: block;
}
.footer {
  text-align: center;
}
.footer a {
  color: #53b2c8;
}

/* form starting stylings ------------------------------- */
.group {
  position: relative;
  margin-bottom: 45px;
}
input {
  font-size: 18px;
  padding: 10px 10px 10px 5px;
  display: block;
  width: 10vw;
  border: none;
  border-bottom: 1px solid #757575;
}
input:focus {
  outline: none;
}

/* LABEL ======================================= */
label {
  color: #999;
  font-size: 18px;
  font-weight: normal;
  position: absolute;
  pointer-events: none;
  left: 5px;
  top: 10px;
  transition: 0.2s ease all;
  -moz-transition: 0.2s ease all;
  -webkit-transition: 0.2s ease all;
}

/* active state */
input:focus ~ label,
input:valid ~ label {
  top: -20px;
  font-size: 14px;
  color: #5264ae;
  color: seagreen;
}

/* BOTTOM BARS ================================= */
.bar {
  position: relative;
  display: block;
  width: 10vw;
}
.bar:before,
.bar:after {
  content: "";
  height: 2px;
  width: 0;
  bottom: 1px;
  position: absolute;
  background: seagreen;
  transition: 0.2s ease all;
  -moz-transition: 0.2s ease all;
  -webkit-transition: 0.2s ease all;
}
.bar:before {
  left: 50%;
}
.bar:after {
  right: 50%;
}

/* active state */
input:focus ~ .bar:before,
input:focus ~ .bar:after {
  width: 50%;
}

/* HIGHLIGHTER ================================== */
.highlight {
  position: absolute;
  height: 60%;
  width: 100px;
  top: 25%;
  left: 0;
  pointer-events: none;
  opacity: 0.5;
}

/* active state */
input:focus ~ .highlight {
  -webkit-animation: inputHighlighter 0.3s ease;
  -moz-animation: inputHighlighter 0.3s ease;
  animation: inputHighlighter 0.3s ease;
}

/* ANIMATIONS ================ */
@-webkit-keyframes inputHighlighter {
  from {
    background: seagreen;
  }
  to {
    width: 0;
    background: transparent;
  }
}
@-moz-keyframes inputHighlighter {
  from {
    background: seagreen;
  }
  to {
    width: 0;
    background: transparent;
  }
}
@keyframes inputHighlighter {
  from {
    background: seagreen;
  }
  to {
    width: 0;
    background: transparent;
  }
}
